html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f4f4f4;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

.leaflet-control-geocoder {
  z-index: 1000;
}

#login-container {
  text-align: center;
  z-index: 1000;
  background-color: #fff;
  border-radius: 5px;
  width: 300px;
  padding: 20px;
  position: relative;
  box-shadow: 0 0 10px #0000001a;
}

@media (width <= 600px) {
  #login-container {
    width: 90%;
    margin: 0 auto;
    padding: 20px;
  }
}

#login-container h2 {
  color: #333;
  margin: 0 0 20px;
}

#login-container input[type="email"], #login-container input[type="password"] {
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
}

#login-container button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: 10px;
}

#login-container button:hover {
  background-color: #0056b3;
}

#login-error {
  margin-top: 15px;
}

#map {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.leaflet-tooltip {
  background-color: #fffc;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px;
  font-size: 16px;
  font-weight: bold;
  color: #8b0000 !important;
}

.pulse-icon {
  background: #30a2ff99;
  border: 5px solid #30a2ff;
  border-radius: 50%;
  width: 1px;
  height: 1px;
  animation: 1.5s infinite pulse;
  box-shadow: 0 0 #30a2ff66;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 #30a2ff66;
  }

  70% {
    box-shadow: 0 0 0 10px #30a2ff00;
  }

  100% {
    box-shadow: 0 0 #30a2ff00;
  }
}

